import React, { FunctionComponent, useMemo } from 'react';

import ClassNames from './TeaserLanguageSelector.module.scss';
import { ILocale } from '../../../../../types/ILocale';
import { useAppConfig } from '../../../../../contexts/AppConfig/AppConfig';

interface Props {
    className?: string;
}

export function _getNonLocaledPagePath(path: string, sitePath: string | undefined, currentLocaleCode: string): string {
    return path.replace(new RegExp(`^/${sitePath}/?`), '').replace(new RegExp(`^/?(${currentLocaleCode})/?`), '');
}

export function _getUiLocales(
    locales: ILocale[],
    currentLocaleCode: string,
    path: string
): { href: string; name: string }[] {
    const sitePath = (process.env.SITE_PATH || '').replace(/^\//, '');
    const nonLocaledPath = _getNonLocaledPagePath(path, sitePath, currentLocaleCode);
    return locales.map(locale => {
        return {
            href: `${sitePath ? '/' + sitePath : ''}/${locale.default ? '' : locale.code + '/'}${nonLocaledPath}`,
            name: locale.shortName,
        };
    });
}

const TeaserLanguageSelector: FunctionComponent<Props> = ({ className }) => {
    const { locales, currentLocaleCode, slug } = useAppConfig();

    const parsedLocales = useMemo(_getUiLocales.bind(null, locales, currentLocaleCode, slug), []);

    return (
        <div className={className}>
            <ul className={ClassNames.TeaserLanguageSelector}>
                {parsedLocales.map(locale => (
                    <li key={locale.name}>
                        <a href={locale.href}>{locale.name}</a>
                    </li>
                ))}
            </ul>
        </div>
    );
};

export default TeaserLanguageSelector;
